import React from "react";
import './footer.css'
import Form from 'react-bootstrap/Form'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLaptopCode } from '@fortawesome/free-solid-svg-icons'
import { faSurprise } from '@fortawesome/free-solid-svg-icons'
import { faHashtag } from '@fortawesome/free-solid-svg-icons'





export const Footer = () => {
  return (

    <div className='newcaster'>
<div className="grupo1">

<div className="apresentafooterblog">
        <h3>blog</h3>
        <p>Bem-vindo ao nosso Portal de Notícias completo sobre vida, estilo de vida,
          moda e bem-estar. Tome seu tempo e mergulhe
          nesta experiência incrível!</p>
      </div>

      <div className="contatofooterblog">
        <div><h3>Sobre</h3></div>
        <div><p>Sobre</p></div>
        <div><p>contato</p></div>

      </div>


</div>
      
     <div className="grupo2">
     <div className="redesociaisfooterblog">
        <div><h3>siga-nos</h3></div>
        <div className="coluna1redesfooter">
        <div><FontAwesomeIcon icon={faLaptopCode} /> </div>
        <div><FontAwesomeIcon icon={faSurprise} /> </div>
        </div>
        <div className="coluna1redesfooter">
        <div><FontAwesomeIcon icon={faSurprise} /> </div>
        <div><FontAwesomeIcon icon={faHashtag} /> </div>

        </div>
      </div>
      <div className="subscriptfooter">
      <Form.Group className="mb-3" controlId="formBasicEmail">
    <Form.Label>Inscreva-se</Form.Label>
    <Form.Control type="email" placeholder="Enter email" />
    <Form.Text className="text-muted">
      We'll never share your email with anyone else.
    </Form.Text>
  </Form.Group>
      </div>

    </div>

     </div>


     

  )
}