import React from "react";
import { Navecagao } from "../navbar/navegacao";
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import { useDispatch } from "react-redux";
import { loginUser } from "../../reducers/user";
import { useState } from "react";


import './login.css'

export const Login = () => {

    const [pegaemail,setemail] = useState()
    const [pegasenha , setsenha] = useState()

   
    

    const dispatch = useDispatch()

       const enviarusuario = ()=>{
           dispatch(loginUser({
               usuario:pegaemail,
               senha:pegasenha
           }))

           window.location = `https://fernandorrn3.github.io/projeto-blog-redux/?#/${pegaemail}/adm` 
       }

    return (

        <div className="loginPage">
            <Navecagao />
            <div className="loginContent">


                <div className="loginarea"> 
                    
                    <Form>
                    <Form.Group className="mb-3" controlId="formBasicEmail">
                        <Form.Label>Email address</Form.Label>
                        <Form.Control type="email" placeholder="Enter email" onChange={(e) => setemail(e.target.value)}/>
                        <Form.Text className="text-muted">
                            We'll never share your email with anyone else.
                        </Form.Text>
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formBasicPassword">
                        <Form.Label>Password</Form.Label>
                        <Form.Control type="password" placeholder="Password" onChange={(e)=> setsenha(e.target.value)} />
                    </Form.Group>
                    
                    <Button variant="primary" type="submit" onClick={enviarusuario}>
                        Submit
                    </Button>
                </Form></div> 
            </div>
        </div>
    )
}